<script type="text/javascript">
const acg_colos = [
  "#e62d73",
  "#0089ff",
  // "#b3a700",
  "#fff233",
  "#19ffa8",
  "#8600cc",
  "#00024d",
  "#fbd7be",
];
const acg_colos_info = [
  "#e62d73",
  "#0089ff",
  "#b3a700",
  "#19ffa8",
  "#8600cc",
  "#00024d",
  "#c8a992",
];
const acg_type = [
  "喜欢",
  "准备看",
  "没看完",
  "已看完",
  "不想看",
  "黑名单",
  "正在看",
];
const errorimg = 'this.src="' + require("@/assets/error.jpg") + '"';
const type_sw = Array(acg_type.length).fill(false);
const Main_url = "https://www.agemys.cc";
import axios from "axios";

const loadweek = function () {
  var _this = this;
  axios
    .get("https://api.agefans.app/v2/home-list?update=1&recommend=1")
    .then((response) => {
      _this.saveweeknew = response.data.XinfansInfo;
    });
};

const isNew = function (AID) {
  for (let index = 0; index < this.saveweeknew.length; index++) {
    if (this.saveweeknew[index].id == AID) {
      if (this.saveweeknew[index].isnew) {
        return true;
      }
    }
  }
  return false;
};

export default {
  loadweek,
  isNew,
  errorimg,
  acg_colos,
  acg_type,
  type_sw,
  Main_url,
  acg_colos_info,
  saveweeknew: [],
  methods: {},
  mounted() {
  },
};
</script>